import * as d3 from "d3";
import './Map.style.css';
import MapImage from './details/MapImage';
import Field from '../../components/field/Field';


export default class Map extends React.Component {

    state = {
        shopCoordinates: {width: 200, height: 400, x: 100, y: 0},
        point: { x: 800, y: 400 },
        appliedShopCoordinates: {width: 200, height: 400, x: 100, y: 0},
        appliedPoint: { x: 800, y: 400 },
    }

    componentDidMount() {
        // this.applyZoom()
    }

    applyZoom = () => {
        let { appliedShopCoordinates } = this.state;
        let width = 1745;
        let height = 1400;
        let zoom = d3.zoom()
            .scaleExtent([1, 5])
            .translateExtent([[0, 0], [width+100, height+80]])
            .on("zoom", this.move);
        d3.select("svg").call(zoom);
        // d3.select("svg").on("dragstart", function () { d3.event.sourceEvent.preventDefault(); });
    }

    move = () => {
        let transform = d3.event.transform;

        d3.select("#map").attr("transform", transform);
    }

    onChange = (e) => {
        let newShopCoordinates = JSON.parse(JSON.stringify(this.state.shopCoordinates));
        newShopCoordinates[e.name] = e.value || '';
        console.log(newShopCoordinates)
        this.setState({ shopCoordinates: newShopCoordinates });
    }

    onApply = () => {
        const { shopCoordinates } = this.state;
        let appliedShopCoordinates = JSON.parse(JSON.stringify(shopCoordinates));
        this.setState({ appliedShopCoordinates, appliedPoint: { ...this.state.point } })
    }

    render() {
        return (
            <div>
                <div className="map">
                    <svg ref='map' version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="100%" viewBox="0 0 2048 1638" enableBackground="new 0 0 2048 1638" xmlSpace="preserve">
                        <g id='map'>
                            <MapImage />
                            <rect x={this.state.appliedShopCoordinates.x} y={this.state.appliedShopCoordinates.y} width={this.state.appliedShopCoordinates.width} height={this.state.appliedShopCoordinates.height} style={{ fill: 'blue', stroke: 'pink', strokeWidth: 5, fillOpacity: 0.1, strokeOpacity: 0.9 }} />
                            <g>
                                <rect x={this.state.appliedPoint.x} y={this.state.appliedPoint.y} width={100} height={40} style={{ fill: 'black' }} />
                                <text x={Number(this.state.appliedPoint.x) + 10} y={Number(this.state.appliedPoint.y) + 25} style={{ fill: 'white', fontSize: '23px' }}>Я-текст</text>
                            </g>
                        </g>
                    </svg>
                </div>
                <div className='map-coordinates'>
                    <h3>Введите координаты прямоугольника</h3>
                    <div>
                        <h4>Координаты ЛВУ</h4>
                        <Field name='x' value={this.state.shopCoordinates.x === 0 || this.state.shopCoordinates.x ? this.state.shopCoordinates.x : ''} onChange={this.onChange} label='X ' />
                        <Field name='y' value={this.state.shopCoordinates.y === 0 || this.state.shopCoordinates.y ? this.state.shopCoordinates.y : ''} onChange={this.onChange} label='Y ' />
                    </div>
                    <div>
                        <h4>Размеры области</h4>
                        <Field name='width' value={this.state.shopCoordinates.width === 0 || this.state.shopCoordinates.width ? this.state.shopCoordinates.width : ''} onChange={this.onChange} label='Ширина ' />
                        <Field name='height' value={this.state.shopCoordinates.height === 0 || this.state.shopCoordinates.height ? this.state.shopCoordinates.height : ''} onChange={this.onChange} label='Высота ' />
                    </div>
                    <h3>Введите координаты точки</h3>
                    <div>
                        <Field name='pointX' value={this.state.point.x} onChange={(e) => { this.setState({ point: { ...this.state.point, x: e.value } }) }} label='Координата x ' />
                        <Field name='pointY' value={this.state.point.y} onChange={(e) => { this.setState({ point: { ...this.state.point, y: e.value } }) }} label='Координата y ' />
                    </div>
                    <div className='btn' onClick={this.onApply}>Применить</div>
                </div>
            </div>
        )
    }
}


