import { createBrowserHistory, createHashHistory } from 'history';
import { configureStore } from './store/createStore';
import { RootComponent } from './routes/Root';
import { Provider } from 'react-redux';
import './app.css';

// if (process.env.NODE_ENV === 'development') {
//   window['Utils'] = Utils;
// }

const createHistory = (options = {}) => process.env.NODE_ENV === 'development' ? createHashHistory(options) : createBrowserHistory(options);

// Подрубаем модалку для взаимодействия с React-router-dom Prompt
// export const history = createHistory({
// 	getUserConfirmation: (message, next) => {
// 		promptModal.show({ message })
// 			.then(() => next(true))
// 			.catch(() => next(false));
// 	}
// });

const browserHistory = createHistory();

// историю в глобал
window['currentHistory'] = history;

const store = configureStore(history);

/**
 * Рендер рута
 * @param {any} RootComponent 
 */
const renderApp = (RootComponent) => {
	ReactDOM.render(
		<Provider store={store}  >
			<RootComponent history={browserHistory} />
		</ Provider>,
		document.getElementById('root')
	);
};

// редерим рут
renderApp(RootComponent);

// хот релод
if (module.hot) {
	module.hot.accept('./routes/Root.jsx', () => renderApp(require('./routes/Root').RootComponent) );
}



