



export default class Field extends React.Component {

    onChange = (e) => {

        this.props.onChange({ props: this.props, value: e.currentTarget.value, name: this.props.name });
    }

    render() {
        return (
            <div className='field'>
                <label htmlFor={this.props.name}>{this.props.label}</label>
                <input name={this.props.name} id={this.props.name} type="text" className='field__input' onChange={this.onChange} value={this.props.value} />
            </div>
        )
    }
}


